














.welcome {
  max-width: 450px;
  margin-top: 100px
}
.welcome h2 {
  font-size: 3em;
}
.welcome button {
  margin:2em;
}


.chat h2 {
    font-size: 2.5em;
    margin-bottom: 40px;
}
.chat span {
    font-size: 1.4em;
}
.chat .time{
    font-size: 1em;
    display: block;
}
.messages{
    max-height: 300px;
    overflow: auto;
}
.messages::-webkit-scrollbar{
    width: 3px;
}
.messages::-webkit-scrollbar-track{
    background: #ddd;
}
.messages::-webkit-scrollbar-thumb {
    background: rgb(46, 45, 45);
}

